import { Box, Container, Divider, List, ListItem, ListItemText, ListSubheader, Paper, Typography } from "@material-ui/core";
import { useHistory, useParams } from "react-router";
import ScrollToTop from './ScrollToTop';

let Cateogary = () => {
    let { city } = useParams();
    let history = useHistory();



    let cats = (() => {
        let arr = []
        for (let i = 0; i < 15; i++) {
            arr.push(`Speciality : ${i}`)
        }
        return arr;
    })()

    let genListItems = (items) => {

        let out = []
        if (items) {
            items.map((x) => {
                out.push(
                    <ListItem button onClick={() => history.push(`${history.location.pathname}/cat-${x}`)}>
                        <ListItemText primary={x} />
                    </ListItem>
                )
                out.push(
                    <Divider variant='middle'></Divider>
                )
            })
        }

        return out;
    }

    return (
        <Container maxWidth={'sm'} >
            <ScrollToTop></ScrollToTop>
            {/* <div>Cateogaray : city {city}</div> */}

            <Box mt={4} mb={4}>
                <Typography variant={'h4'}>Select Speciality</Typography>
            </Box>

            <Paper elevation={3}>
                <List subheader={<ListSubheader>Speciality</ListSubheader>}>
                    {genListItems(cats)}
                </List>
            </Paper>

        </Container>
    )
}

export default Cateogary;