import './App.css';
import { Box, createMuiTheme, ThemeProvider, Typography } from "@material-ui/core"
import Navbar from './Components/Navbar';
import { indigo, teal } from '@material-ui/core/colors';
import Homepage from './Components/HomePage';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Cateogary from './Components/Cateogary';
import Doctors from './Components/Doctors';


let theme = createMuiTheme({
  palette: {
    primary: indigo,
    secondary: teal,
    tertiary: teal,
  },
})



function App() {
  return (
    <ThemeProvider theme={theme}>

      <BrowserRouter>

        <Navbar></Navbar>

        <Switch>

          <Route exact path='/'>
            <Homepage></Homepage>
          </Route>

          <Route exact path='/city-:city'>
            <Cateogary></Cateogary>
          </Route>

          <Route exact path='/city-:city/cat-:cat'>
            <Doctors></Doctors>
          </Route>

        </Switch>

        <Box m={2}>
          <Typography variant='h6' align='center' color='textSecondary'>project-doctors</Typography>
        </Box>

      </BrowserRouter>
    </ThemeProvider>
  );
}

export default App;
