import { Box, Button, Card, CardActionArea, CardActions, CardContent, CardMedia, Container, Grid, makeStyles, Typography } from "@material-ui/core";
import { useParams } from "react-router";
import ScrollToTop from "./ScrollToTop";

let Doctors = () => {
    let { cat, city } = useParams();

    let doctors = ["sadf", "sadffsd", "asdfw ", "asdfs", "daf"]

    let useStyles = makeStyles({
        media: {
            height: "140px"
        },
        cardStyle: {
            maxWidth: 345
        },
        cardHolder: {
            alignItems: 'center',
            justifyContent: 'center'
        }
    })

    let classes = useStyles();

    let genList = (doctors) => {
        let output = []
        doctors.map((x) => {

            output.push(
                <Grid item xs={12} sm={6} md={4} style={{ display: "flex", justifyContent: "center" }}>

                    <Card className={classes.cardStyle} >
                        <CardActionArea>
                            <CardMedia
                                className={classes.media}
                                image={"https://post.healthline.com/wp-content/uploads/2020/08/Doctors_For_Men-732x549-thumbnail-1-732x549.jpg"}
                            />

                            <CardContent>
                                <Typography variant="h5" component='h2'>
                                    Doctor Name
                                </Typography>
                                <Typography variant="body2" color="textSecondary" component="p">
                                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Impedit itaque sit architecto iste corrupti molestiae voluptatibus fugit deleniti suscipit. Necessitatibus.
                                </Typography>
                            </CardContent>
                        </CardActionArea>

                        <CardActions>
                            <Button size="small" color="primary">Learn More</Button>
                        </CardActions>
                    </Card >
                </Grid >
            )
        })

        return output
    }

    return (
        <Container maxWidth={'md'}>
            <ScrollToTop></ScrollToTop>
            <Box mt={3} mb={3}>
                <Typography variant="h2">Doctors</Typography>
            </Box>

            <Grid container spacing={2} justify="center">
                {genList(doctors)}
            </Grid>
        </Container>
    )
}

export default Doctors;