import { Container, Box, Button, Grid, TextField, Typography, Paper, Card, CardActions, CardActionArea, CardMedia, CardContent, makeStyles } from "@material-ui/core"
import { Autocomplete } from "@material-ui/lab"
import { useState } from "react";
import { useHistory } from "react-router";




let Homepage = () => {
    let [city, setcity] = useState('null');
    // let [inputCity, setInputCity] = useState('null');

    let history = useHistory();

    let handleCity = (event, newValue) => {
        // console.log(newValue)
        setcity(newValue)
    }

    let goTo = () => {
        // console.log("goto : ", inputCity, city)
        history.push(`${history.location.pathname}\city-${city}`)
    }


    let useStyles = makeStyles({
        media: {
            height: "140px"
        },
        cardStyle: {
            maxWidth: 345
        },
        cardHolder: {
            alignItems: 'center',
            justifyContent: 'center'
        }
    })

    let classes = useStyles();

    let returnBox = () => {
        let out = []
        for (let i = 0; i < 3; i++) {
            out.push(
                <Grid item xs={12} sm={6} md={4} style={{ display: "flex", justifyContent: "center" }}>

                    <Card className={classes.cardStyle} >
                        <CardActionArea>
                            <CardMedia
                                className={classes.media}
                                image={"https://post.healthline.com/wp-content/uploads/2020/08/Doctors_For_Men-732x549-thumbnail-1-732x549.jpg"}
                            />

                            <CardContent>
                                <Typography variant="h5" component='h2'>
                                    Read reviews from users
                                </Typography>
                                <Typography variant="body2" color="textSecondary" component="p">
                                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Impedit itaque sit architecto iste corrupti molestiae voluptatibus fugit deleniti suscipit. Necessitatibus.
                                </Typography>
                            </CardContent>
                        </CardActionArea>

                        <CardActions>
                            <Button size="small" color="primary">Learn More</Button>
                        </CardActions>
                    </Card >
                </Grid >
            )
        }

        return out;
    }

    let cities = ["Betul", "Bhopal", "Delhi", "Mumbai", "Chennai", "Varanasi", "option x", "option y", "option z"]

    return (
        <Container maxWidth={'md'}>
            <Grid container direction="column">

                <Grid item>
                    <Typography variant='h2' align='center' style={
                        { height: "250px", display: 'flex', justifyContent: "center", alignItems: "center" }
                    }>Project Logo</Typography>
                </Grid>

                <Grid item container justify='center' alignItems='center' spacing={4}>

                    <Grid item xs={12} md={10}>
                        <Box p={0}>
                            <Autocomplete
                                options={cities}
                                getOptionLabel={(x) => x}
                                onChange={handleCity}
                                renderInput={(x) => <TextField {...x} label="Enter City" variant='outlined'></TextField>}
                            ></Autocomplete>
                        </Box>
                    </Grid>

                    <Grid item md={2}>
                        <Button variant='contained' color='primary' onClick={() => goTo()}>Search</Button>
                    </Grid>

                </Grid>

            </Grid>

            <Box mt={8}>
                <Grid container spacing={2}>
                    {returnBox()}
                </Grid>
            </Box>

        </Container >
    )
}

export default Homepage;